import React from 'react';
import { useState, useEffect } from "react";
import { ethers } from "ethers";
import './style/Minter.css';
import contract_ABI from "./web3/ppABI.json";
import contract_ADDRESS from "./web3/ppAddress.js";
import Viewer from "./Viewer.js";

const THETA_NETWORK_ID = 365;
const TEN18 = 1000000000000000000n;
const BASEPRICE = 1;
const PRICEINCREASE1 = 1;
const PRICEINCREASE2 = 1;
var app_provider; 
var app_contract;
var user_provider;
var user_contract;

const Minter = () => {
    const [account, setAccount] = useState(null);
    // User account.
    const [tokenCount, setTokenCount] = useState('0');
    // User number of NFTs.
    const [mintAmmount, setMintAmmount] = useState("1");
    // Number of NFTs to mint.
    const [viewerFlag, setViewerFlag] = useState(false);
    // Flag to sho/hide token viewer.
    const isConnected = Boolean(account);
    // Connected flag.
    const [timesReloaded, setTimesReloaded] = useState(0);
    //To update Viewer for each click.
    const [isLoading, setIsLoading] = useState(false);
    // To avoid overloading the viewer.



    // // METAMASK APP // //
    
    async function connectUserContract() {
        user_provider = new ethers.BrowserProvider(window.ethereum);
        console.log('Got user provider.');
        const signer = await user_provider.getSigner();
        console.log('Got user signer.');
        user_contract = new ethers.Contract(
            contract_ADDRESS,
            contract_ABI,
            signer
        );
        console.log('Connected to user contract.');
    }

    async function userOwns(account_) {
        let ownedAmmount = await user_contract.balanceOf(account_);
        setTokenCount(ownedAmmount.toString());
        console.log('Token count updated to:', ownedAmmount.toString(),'.');
    }

    async function handleChainChanged(networkId_hex) {
        const networkId = parseInt(networkId_hex);
        if (networkId === THETA_NETWORK_ID) {
            const accounts_ = await window.ethereum
            .request({ method: 'eth_requestAccounts'})
            .catch((err) => {
            if (err.code === 4001) {
                // EIP-1193 userRejectedRequest error.
                // User rejected connection request.
                console.log('Connect to MetaMask');
            } else {
                console.error(err);
            }})
        
            try {
                setAccount(accounts_[0]);
                console.log( "Connected to:" );
                console.log(accounts_[0]);
                await connectUserContract();
                await userOwns(accounts_[0])
            } catch (err) {
                console.error(err);
            }
        } else {
            setAccount('ERROR: Connect MetaMask to Theta Network.');
            console.log('Incorrect chain: ', networkId);
        }
    }

    async function handleAccountsChanged(accounts_) {
        if (accounts_.length === 0) {
            // This is to fix lock Metamask bug (wallet closing event is not fired).
            setAccount(null);
            console.log('Disconnected from MetaMask.');
            setViewerFlag(false);
        } else if (accounts_[0] !== account) {
            try {
                setAccount(accounts_[0]);
                console.log('Account changed to:');
                console.log(accounts_[0]);
                await connectUserContract();
                await userOwns(accounts_[0]);
            } catch(err) {
                console.log(err);
            }
        } else {
            console.log('handleAccountsChanged(...) was casted but produced no change.');
            // Sometimes a MetaMask bug occurs.
        }
    }

    function handleDisconnect() {
        setAccount(null);
        console.log('Metamask Disconnected.')
    }

    function metamaskListener(){
        window.ethereum.on('accountsChanged', handleAccountsChanged);
        window.ethereum.on('chainChanged', handleChainChanged);
        window.ethereum.on('disconnect', handleDisconnect);
        console.log('Listeners called.')
    }

    async function loadMetaMask() {
        const accounts_ = await window.ethereum
        .request({ method: 'eth_requestAccounts'})
        .catch((err) => {
            if (err.code === 4001) {
                // EIP-1193 userRejectedRequest error.
                // User rejected connection request.
                console.log('Connect to MetaMask');
            } else {
                console.error(err);
            }
        })
        
        const networkId_hex = await window.ethereum
        .request({ method: 'eth_chainId' })
        .catch((err) => {console.log(err)});
        const networkId = parseInt(networkId_hex);
        
        metamaskListener();
        
        if (networkId === THETA_NETWORK_ID) {
            try {
                setAccount(accounts_[0]);
                console.log( "Connected to:" );
                console.log(accounts_[0]);
                await connectUserContract();
                await userOwns(accounts_[0])
            } catch (err) {
                console.error('Waiting for log in. Check your MetaMask.', err);
            }
        } else {
            setAccount('ERROR: Switch MetaMask to Theta Network.');
        }
    }

    function adjustPrice () {
    //EDIT 1000, 3000
        if (appSupply < 10) {
            return BASEPRICE;
        } else if (appSupply < 30) {
            return (BASEPRICE + PRICEINCREASE1);
        } else {
            return (BASEPRICE + PRICEINCREASE1 + PRICEINCREASE2);
        }
    }
    
    async function getAccountBalance(_account) {
        var balance = await app_provider.getBalance(_account);
        return Number( balance / TEN18 );
    }
    
    async function webMint() {
        try {
            var totalPrice = adjustPrice() * mintAmmount;
            console.log('totalPrice:', totalPrice);
            var accountBalance = await getAccountBalance(account);
            console.log('accountBalance:', accountBalance);
            if ( totalPrice <= accountBalance ) {
		    let totalCost = ethers.parseEther( totalPrice.toString() );
		    const response = await user_contract.mintForSelf(mintAmmount, {value: totalCost});
		    console.log('response: ', response);
		    await userOwns(account);
		    const receipt = await response.wait();
		    console.log('receipt: ', receipt);
	    } else if ( totalPrice > accountBalance ) {
		alert('Not enough TFUEL !');
	    }
        } catch (err) {
            console.log('error: ',err);
        }
    }

    // // APP BUTTONS // //
    function MintAmmountPicker({mintAmmount, setMintAmmount}) {    
        return(
        <select 
            className="selecter-props"
            name="mint_ammount" 
            value={mintAmmount}
            onChange={e => setMintAmmount(e.target.value)}
        >
            <option value="1">1</option>;
            <option value="2">2</option>;
            <option value="3">3</option>;
            <option value="4">4</option>;
            <option value="5">5</option>;
            <option value="6">6</option>;
            <option value="7">7</option>;
            <option value="8">8</option>;
            <option value="9">9</option>;
            <option value="10">10</option>;
        </select>
        )
    }
    // // LOAD COUNTER // //
    // Load the app and pass down the total count (appSupply variable).
    const [appSupply, setAppSupply] = useState('- - - -');

    async function connectAppContract() {
        app_provider = new ethers.getDefaultProvider("https://eth-rpc-api-testnet.thetatoken.org/rpc");
        /// Update for not local.
        console.log('* Got app provider.');
        app_contract = new ethers.Contract(
            contract_ADDRESS,
            contract_ABI,
            app_provider
        );
        console.log('* Connected app to contract.');
    }

    async function checkTotalSupply() {
        console.log('* Checking total supply.')
        const totalSupply = await app_contract.totalSupply();
        const totalSupply_str = totalSupply.toString();
        console.log('* Retrieved: ', totalSupply_str);
        if (totalSupply_str !== appSupply) {
            setAppSupply(totalSupply_str);
        }
    }

    async function supplyListener() {
        app_provider.on("block", checkTotalSupply);
        console.log('* App listener casted.');
    }


    // // SHOW VIEWER OPTION// //
    const stopAndRender = () => new Promise((resolve) => setTimeout(resolve,0));

    async function showViewer() {
        try {
            if (isLoading === false) {
                setIsLoading(true);
                await stopAndRender();
                if (viewerFlag === true) {
                    setTimesReloaded(timesReloaded + 1);
                } else if (viewerFlag === false) {
                    setViewerFlag(true);
                }
            }
        } catch (err) {
            console.log("Can't load your tokens.", err);
        }
    }


    // // CONNECT APP TO CONTRACT AFTER LOAD. // //
    useEffect(() => {
        console.log('Minter useEffect 1 casted.');

        try {
            connectAppContract();
            checkTotalSupply();
            supplyListener();
        } catch (err) {
            console.log("* Can't load app token counter: ", err);
        }// eslint-disable-next-line
    }, []);

    return (
        <div className="container Minter-main-container" id="Minter" >
            <div className="content Minter-second-container">
                <h5 className="center padding-32"><u>GET YOURS!</u></h5>
                <div className="Minter-third-container">
                    <p>Supply: {appSupply} / 5000.</p>
                    <div className="tiny-font">
                        <p>First 1000, 100 tfuel.</p>
                        <p>Next 2000, 150 tfuel.</p>
                        <p>Last 2000, 200 tfuel.</p>
                    </div>
                </div>
                <div className="round padding-32 Minter-box">  
                    {isConnected ? (
                        <div>
                            <p>Connected to:</p>
                            <p>{account}</p>
                            <p>&nbsp;</p>
                            <MintAmmountPicker className="ammount-button" mintAmmount={mintAmmount} setMintAmmount={setMintAmmount}/>
                            <button className="mint-button" onClick={webMint}>Mint !</button>
                            <div>&nbsp;</div>
                            <div>&nbsp;</div>
                            <p>You own {tokenCount} tokens.</p>
                            {isLoading ? (
                                <p className="tiny-font">loading...</p>
                                ) : (
                                <p className="tiny-font">Click <a onClick={showViewer} className="Viewer-opener hover-text-green"><u>here</u></a> to see your PP.</p>

                            )}
                        </div>
                    ) : (
                        <div>
                            <div>&nbsp;</div>
                            <div>&nbsp;</div>
                            <p>CONNECT:</p>
                            <div>&nbsp;</div>
                            <div>
                                <button className="connect-button" onClick={loadMetaMask}>Connect with MetaMask</button>
                            </div>
                            <div>&nbsp;</div>
                            <div>&nbsp;</div>
                            <div>&nbsp;</div>
                            <div>&nbsp;</div>
                        </div>
                    )}
                </div>  
            
            </div>
        </div>
    )
}

export default Minter;
